import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { FeedbackListComponent } from './feedback-list.component';
import { FeedbackService } from 'src/app/service/feedback.service';

const routes: Routes = [
  {
    path: '',
    component: FeedbackListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../feedback-add/feedback-add.module').then(m => m.FeedbackAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../feedback-add/feedback-add.module').then(m => m.FeedbackAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    FeedbackListComponent
  ],
  imports: [
    CommonModule,
    LoderModule,
    PaginationModule,
    // RatingModule ,
    RouterModule.forChild(routes)
  ],
  providers: [
    FeedbackService
  ]
})


export class FeedbackListModule { }
